<template>
  <div>
    <b-form @submit="onSubmit" @reset="onReset" v-if="showSearch" inline class="mb-2">
      <b-input-group prepend="Name">
        <b-form-input v-model="search.name" type="text"></b-form-input>
      </b-input-group>
      <b-button type="submit" variant="outline-primary" class="ml-2"><i class="fa fa-search fa-fw fa-lg"></i></b-button>
      <b-button type="reset" variant="secondary" class="ml-1"><i class="fa fa-refresh fa-fw fa-lg"></i></b-button>

      <b-button variant="primary" class="ml-auto" @click="onAddClick">
        <i class="fa fa-plus fa-fw fa-lg"></i>&nbsp;Add
      </b-button>
    </b-form>
    <b-row>
      <b-col sm="6" md="4" v-for="i in items" v-bind:key="i">
        <b-card>
          <div slot="header">
            <span style="color: blue;"><i class="fa fa-file-pdf-o fa-fw fa-lg" v-if="i < 6"></i><i class="fa fa-file-word-o fa-fw fa-lg" v-if="i > 5"></i></span> Title
            <b-badge pill variant="success" class="float-right">{{ randomNumber() }}</b-badge>
          </div>
          Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat.
        </b-card>
      </b-col>
    </b-row>
  </div>
</template>

<script>
export default {
  name: "Library",
  data: function() {
    return {
      items: [1,2,3,4,5,6,7,8,9],
      search: {
        name: ""
      },
      showSearch: true,
      IsAddAllowed: true
    };
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
    },
    onReset (evt) {
      evt.preventDefault();
      this.search.name = '';
      /* Trick to reset/clear native browser form validation state */
      this.showSearch = false;
      this.$nextTick(() => { this.showSearch = true });
    },
    onAddClick() {
      //this.$router.push('new')
    },
    randomNumber : function(){
      return Math.floor(Math.random() * (10 - 1 + 1)) + 1;
    }
  }
};
</script>